import React, {useState, useEffect} from 'react';
// import { useHistory } from "react-router-dom";
import axios from 'axios';
import './DogPage.css';
import NavbarBlack from '../Navbar/NavbarBlack';
import { CircleProgress } from 'react-gradient-progress'

const DogPage = ({ match }) => {
  const [data, setData] = useState([{}]);
  const [fysik, setFysik] = useState(null);
  const [ledarskap, setLedarskap] = useState(null);
  const [allergi, setAllergi] = useState(null);
  const [storlek, setStorlek] = useState(null);
  const [seeEmail, setSeeEmail] = useState(false);

  let params = match.params;
  const id = params.id

  const showEmail = () => {
    setSeeEmail(!false);
  }

    useEffect(() => {
      const fetchDogs = async () => {
        const result = await axios(`https://matchingpaws-api.herokuapp.com/dogs/${id}`);
        setData(result.data);
      };
      fetchDogs();
    }, [id]);

    const handlePercentageFysik = () => {
      let percentageFysik = null;

      if (data?.fysik === 'låg') {
        percentageFysik = 10;
      }
      if (data?.fysik === 'normal') {
        percentageFysik = 50;
      }
      if (data?.fysik === 'hög') {
        percentageFysik = 100;
      }
      setFysik(percentageFysik)
    };

    const handlePercentageLedarskap = () => {
      let percentageLedarskap = null;

      if (data?.ledarskap === 'mjukt') {
        percentageLedarskap = 100;
      }
      if (data?.ledarskap === 'tydligt') {
        percentageLedarskap = 20;
      }
      if (data?.ledarskap === 'båda') {
        percentageLedarskap = 100;
      }
      setLedarskap(percentageLedarskap)
    }

    const handlePercentageAllergi = () => {
      let percentageAllergi = null;

      if (data?.allergi === 'ja') {
        percentageAllergi = 0;
      }
      if (data?.allergi === 'nej') {
        percentageAllergi = 100;
      }
      setAllergi(percentageAllergi)
    }

    const handlePercentageStorlek = () => {
      let percentageStorlek = null;

      if (data?.storlek === 'liten') {
        percentageStorlek = 10;
      }
      if (data?.storlek === 'mellan') {
        percentageStorlek = 50;
      }
      if (data?.storlek === 'stor') {
        percentageStorlek = 100;
      }
      setStorlek(percentageStorlek)
    }

    useEffect(() => {
      if (!seeEmail) {
        window.scrollTo(0, 0);
      }
    })
    useEffect(() => {
      handlePercentageFysik()
      handlePercentageLedarskap()
      handlePercentageAllergi()
      handlePercentageStorlek()
    }, [data])

    console.log('data', data)


    return (
      <>
      <NavbarBlack color="black" />
        <div className="contentWrapper">
      {/* <Link to="/adoptera"><button className="buttonLink">Gå tillbaka</button></Link> */}
      {/* <button onClick={history.goBack}>Go back</button> */}
        <div className="dogpage-container">
          <div className="dogpage-image-wrapper">
            <img alt="dog" src={data.bild} className="dogpage-image"/>
          </div>
          <div className="dogpage-info-wrapper">
            <h2>{data.hundnamn}</h2>
            <div>
              <p>Ras: {data.ras}</p>
              <p>Ålder: {data.alder} år</p>
              <p>Storlek: {data.storlek}</p>
              <p>Kastrerad: {data.kastrerad}</p>
            </div>
            <div className="descriptionWrapper">
              <p><i>{data.beskrivning}</i></p>
            </div>
          </div>
        </div>
        <div className="circleProgressWrapper">
          <div className="circleWrapper">
            <CircleProgress percentage={fysik} width={100} strokeWidth={5} primaryColor="#000000" secondaryColor="#f0f0f0" />
            <h5>Fysisk förmåga</h5>
          </div>
          <div className="circleWrapper">
            <CircleProgress percentage={ledarskap} width={100} strokeWidth={5} primaryColor="#000000" secondaryColor="#f0f0f0" />
            <h5>Mjukt ledarskap</h5>
          </div>
          <div className="circleWrapper">
            <CircleProgress percentage={allergi} width={100} strokeWidth={5} primaryColor="#000000" secondaryColor="#f0f0f0" />
            <h5>Fäller päls</h5>
          </div>
          <div className="circleWrapper">
            <CircleProgress percentage={storlek} width={100} strokeWidth={5} primaryColor="#000000" secondaryColor="#f0f0f0" />
            <h5>Mankstorlek</h5>
          </div>
        </div>
        <div>
          <div className="checkboxWrapper">
            {data?.katt === 'ja' ? 
            <span className="successIcon"><i class="fas fa-check"></i></span> 
            : <span className="declineIcon"><i class="fas fa-times"></i></span>
            }
            <h5>Kan bo med katt</h5>
          </div>
          <div className="checkboxWrapper">
            {data?.barn === 'ja' ?
            <span className="successIcon"><i class="fas fa-check"></i></span>
            : <span className="declineIcon"><i class="fas fa-times"></i></span>
            }
            <h5>Kan bo med småbarn</h5>
          </div>
          <div className="checkboxWrapper">
          {data?.hund === 'tikar' || 'hanar' || 'båda' ?
            <span className="successIcon"><i class="fas fa-check"></i></span>
            : <span className="declineIcon"><i class="fas fa-times"></i></span>
          }
            <h5>Kan bo med andra hundar</h5>
          </div>
        </div>
          {/* <div className="kravContainer">
            <div className="kravWrapper">
              <p>Fäller päls</p>
              {data.allergi === 'ja' && <span className="successIcon"><i class="fas fa-check"></i></span>}
              {data.allergi === 'nej' && <span className="declineIcon"><i class="fas fa-times"></i></span>}
            </div>
          </div> */}
          <div className="descriptionWrapper">
          <h3>Kontakt</h3>
          <p>För att påbörja en kontakt med ägaren till {data.hundnamn}, kontakta <i>{data.kontaktnamn}</i>.</p>
          {!seeEmail && <button className="buttonLinkSecret" onClick={showEmail}>Klicka här för mailadress</button>}
          <div className="kravButtons">
            {seeEmail && <a className="buttonLink" href={`mailto:${data.email}`}>{data.email}</a>}
            {data.lank && <button href="www.matchingpaws.se" target="_blank" className="button buttonWrapper">Länk till adoptionsannons</button>}
          </div>
        </div>
      </div>
      </>
    )
}

export default DogPage;